"use client";

import { useState, useEffect } from "react";
import { useAwsCredentials } from "@/hooks/useAwsCredentials";
import { motion } from "framer-motion";
import CredentialSelector from "@/components/aws/CredentialSelector";
import { 
  Shield, AlertTriangle, AlertCircle, Server, 
  Key, Database, Network, Lock, Users, 
  CheckCircle, XCircle, Info, RefreshCw
} from "lucide-react";

// Tipos para o EC2
interface EC2Instance {
  id: string;
  type: string;
  state: string;
  publicIp: string;
  privateIp: string;
  launchTime: string;
}

// Tipos para o EC2 Security Service
interface EC2SecurityData {
  results: {
    securityGroups: SecurityGroup[];
    networkAcls: NetworkACL[];
    instances: SecurityInstance[];
    keyPairs: KeyPair[];
    volumes: Volume[];
    iamRoles: IAMRole[];
  };
  summary: {
    totalIssues: number;
    securityGroupIssues: number;
    networkAclIssues: number;
    instanceIssues: number;
    keyPairIssues: number;
    volumeIssues: number;
    iamRoleIssues: number;
  };
}

interface SecurityGroup {
  id: string;
  name: string;
  description: string;
  vpcId: string;
  inboundRules: SecurityGroupRule[];
  outboundRules: SecurityGroupRule[];
  issues: SecurityGroupIssue[];
}

interface SecurityGroupRule {
  protocol: string;
  fromPort: number;
  toPort: number;
  ipRanges: IPRange[];
  ipv6Ranges: IPRange[];
}

interface IPRange {
  cidr: string;
  description?: string;
}

interface SecurityGroupIssue {
  type: string;
  description: string;
  severity: 'LOW' | 'MEDIUM' | 'HIGH' | 'CRITICAL';
  affectedRule: SecurityGroupRule;
}

interface NetworkACL {
  id: string;
  vpcId: string;
  default: boolean;
  inboundRules: ACLRule[];
  outboundRules: ACLRule[];
  issues: ACLIssue[];
}

interface ACLRule {
  ruleNumber: number;
  protocol: string;
  action: string;
  cidrBlock: string;
  fromPort: number;
  toPort: number;
}

interface ACLIssue {
  type: string;
  description: string;
  severity: 'LOW' | 'MEDIUM' | 'HIGH';
  affectedRule: ACLRule;
}

interface SecurityInstance {
  id: string;
  type: string;
  state: string;
  publicIp?: string;
  privateIp?: string;
  subnetId?: string;
  vpcId?: string;
  launchTime?: Date;
  imageId?: string;
  keyName?: string;
  iamInstanceProfile?: string;
  issues: InstanceIssue[];
}

interface InstanceIssue {
  type: string;
  description: string;
  severity: 'LOW' | 'MEDIUM' | 'HIGH';
}

interface KeyPair {
  name: string;
  fingerprint: string;
  issues: KeyPairIssue[];
}

interface KeyPairIssue {
  type: string;
  description: string;
  severity: 'LOW' | 'MEDIUM';
}

interface Volume {
  id: string;
  instanceId?: string;
  size: number;
  type: string;
  encrypted: boolean;
  state: string;
  issues: VolumeIssue[];
}

interface VolumeIssue {
  type: string;
  description: string;
  severity: 'MEDIUM' | 'HIGH';
}

interface IAMRole {
  name: string;
  arn: string;
  instanceProfiles: string[];
  permissions: string[];
  issues: IAMRoleIssue[];
}

interface IAMRoleIssue {
  type: string;
  description: string;
  severity: 'MEDIUM' | 'HIGH';
}

export default function EC2Page() {
  const { selectedCredential } = useAwsCredentials();
  const [instances, setInstances] = useState<EC2Instance[]>([]);
  const [securityData, setSecurityData] = useState<EC2SecurityData | null>(null);
  const [loading, setLoading] = useState(true);
  const [securityLoading, setSecurityLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [securityError, setSecurityError] = useState<string | null>(null);
  const [activeTab, setActiveTab] = useState<'instances' | 'security'>('instances');

  useEffect(() => {
    async function fetchInstances() {
      if (!selectedCredential) {
        setLoading(false);
        setSecurityLoading(false);
        return;
      }

      try {
        setLoading(true);
        setError(null);
        
        console.log(`Buscando instâncias EC2 para credencial: ${selectedCredential.id}`);
        const response = await fetch(`/api/aws/resources?credentialId=${selectedCredential.id}&type=EC2`);
        
        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.error || "Erro ao buscar instâncias EC2");
        }
        
        const data = await response.json();
        console.log("Resposta da API:", data);
        setInstances(data.resources || []);
      } catch (err) {
        console.error("Erro ao buscar instâncias:", err);
        setError(err instanceof Error ? err.message : "Erro ao buscar instâncias");
      } finally {
        setLoading(false);
      }
    }

    async function fetchSecurityData() {
      if (!selectedCredential) return;

      try {
        setSecurityLoading(true);
        setSecurityError(null);
        
        console.log(`Buscando dados de segurança do EC2 para credencial: ${selectedCredential.id}`);
        const response = await fetch(`/api/aws/ec2/security?credentialId=${selectedCredential.id}`);
        
        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.error || "Erro ao buscar dados de segurança do EC2");
        }
        
        const data = await response.json();
        console.log("Resposta da API de segurança:", data);
        setSecurityData(data);
      } catch (err) {
        console.error("Erro ao buscar dados de segurança:", err);
        setSecurityError(err instanceof Error ? err.message : "Erro ao buscar dados de segurança");
      } finally {
        setSecurityLoading(false);
      }
    }

    fetchInstances();
    fetchSecurityData();
  }, [selectedCredential]);

  // Função para obter a cor baseada no estado da instância
  const getStateColor = (state?: string) => {
    switch (state?.toLowerCase()) {
      case "running":
        return "bg-green-100 text-green-800";
      case "stopped":
        return "bg-red-100 text-red-800";
      case "pending":
        return "bg-yellow-100 text-yellow-800";
      case "stopping":
        return "bg-orange-100 text-orange-800";
      case "terminated":
        return "bg-gray-100 text-gray-800";
      default:
        return "bg-gray-100 text-gray-800";
    }
  };
  
  // Função para obter a cor baseada na severidade
  const getSeverityColor = (severity: string) => {
    switch (severity) {
      case "CRITICAL":
        return "bg-red-100 text-red-800";
      case "HIGH":
        return "bg-orange-100 text-orange-800";
      case "MEDIUM":
        return "bg-yellow-100 text-yellow-800";
      case "LOW":
        return "bg-blue-100 text-blue-800";
      default:
        return "bg-gray-100 text-gray-800";
    }
  };
  
  // Função para obter o ícone baseado na severidade
  const getSeverityIcon = (severity: string) => {
    switch (severity) {
      case "CRITICAL":
        return <AlertCircle className="h-4 w-4 text-red-600" />;
      case "HIGH":
        return <AlertTriangle className="h-4 w-4 text-orange-600" />;
      case "MEDIUM":
        return <AlertTriangle className="h-4 w-4 text-yellow-600" />;
      case "LOW":
        return <Info className="h-4 w-4 text-blue-600" />;
      default:
        return <Info className="h-4 w-4 text-gray-600" />;
    }
  };

  return (
    <div>
      <motion.div
        className="mb-6"
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.3 }}
      >
        <div className="flex items-center gap-2 mb-2">
          <Server className="h-6 w-6 text-indigo-600" />
          <h1 className="text-3xl font-bold text-gray-900">Monitoramento EC2</h1>
        </div>
        <p className="mt-2 text-gray-600">
          Visualize e analise a segurança das suas instâncias EC2 na AWS.
        </p>
      </motion.div>

      <CredentialSelector />
      
      {/* Tabs */}
      <div className="flex border-b border-gray-200 mb-6">
        <button
          className={`py-2 px-4 font-medium text-sm flex items-center gap-2 ${
            activeTab === 'instances'
              ? 'border-b-2 border-indigo-500 text-indigo-600'
              : 'text-gray-500 hover:text-gray-700'
          }`}
          onClick={() => setActiveTab('instances')}
        >
          <Server className="h-4 w-4" />
          Instâncias
        </button>
        <button
          className={`py-2 px-4 font-medium text-sm flex items-center gap-2 ${
            activeTab === 'security'
              ? 'border-b-2 border-indigo-500 text-indigo-600'
              : 'text-gray-500 hover:text-gray-700'
          }`}
          onClick={() => setActiveTab('security')}
        >
          <Shield className="h-4 w-4" />
          Segurança
        </button>
      </div>

      {/* Conteúdo das Tabs */}
      {activeTab === 'instances' ? (
        <InstancesTab 
          instances={instances} 
          loading={loading} 
          error={error} 
          selectedCredential={selectedCredential}
          getStateColor={getStateColor}
        />
      ) : (
        <SecurityTab 
          securityData={securityData} 
          loading={securityLoading} 
          error={securityError}
          selectedCredential={selectedCredential}
          getSeverityColor={getSeverityColor}
          getSeverityIcon={getSeverityIcon}
        />
      )}
    </div>
  );
}

// Componente para a aba de instâncias
function InstancesTab({ 
  instances, 
  loading, 
  error, 
  selectedCredential,
  getStateColor 
}) {
  return (
    <motion.div
      className="bg-white rounded-lg shadow-md p-6"
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.3 }}
    >
      {!selectedCredential ? (
        <div className="text-center py-6">
          <p className="text-gray-500">Selecione uma credencial AWS para visualizar as instâncias.</p>
        </div>
      ) : loading ? (
        <div className="flex justify-center py-8">
          <div className="animate-spin rounded-full h-8 w-8 border-t-2 border-b-2 border-indigo-500"></div>
        </div>
      ) : error ? (
        <div className="p-4 bg-red-50 rounded-md">
          <p className="text-red-700">{error}</p>
        </div>
      ) : instances.length === 0 ? (
        <div className="text-center py-6">
          <p className="text-gray-500">Nenhuma instância EC2 encontrada na região {selectedCredential.region}.</p>
          <p className="text-sm text-gray-400 mt-2">
            Verifique se você tem instâncias EC2 nesta região ou se suas credenciais têm permissão para acessá-las.
          </p>
        </div>
      ) : (
        <div>
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-lg font-medium text-gray-900">
              {instances.length} {instances.length === 1 ? "Instância" : "Instâncias"} Encontradas
            </h2>
            <span className="text-sm text-gray-500">
              Região: {selectedCredential.region}
            </span>
          </div>
          
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    ID da Instância
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Tipo
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Estado
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    IP Público
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    IP Privado
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Iniciada Em
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {instances.map((instance) => (
                  <tr key={instance.id} className="hover:bg-gray-50">
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                      {instance.id}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {instance.type}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${getStateColor(instance.state)}`}>
                        {instance.state}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {instance.publicIp || "-"}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {instance.privateIp || "-"}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {instance.launchTime ? new Date(instance.launchTime).toLocaleString() : "-"}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}
    </motion.div>
  );
} 